import React, { useRef } from "react";
import publicURL from "../../../utils/publicURL";
import "./tile.scss";

function Tile({ page, currentPage, numberOfPage, collection }) {
  const refContainer = useRef(null);
  const progress = Math.max(0, currentPage - page);

  // progress       
  // 0.25 -> ,    8%   opacity 1 
  let opacity = Math.min(1, Math.max(0, progress * 4));
  // 0.85 ->  28%   
  if (progress > 0.85 && page < numberOfPage - 1) {
    // 1       0      
    opacity = Math.max(0, (1.0 - progress) * 4);
  }

  let translateY = Math.max(0, 50 - progress * 3 * 50);
  if (progress > 0.85) translateY = Math.max(-50, -(progress - 0.85) * 2 * 50);

  return (
    <div
      ref={refContainer}
      className="tile"
      style={{
        pointerEvents: progress <= 0 || progress >= 1 ? "none" : undefined,
        opacity,
      }}
    >
      <div className="tile-item">
        <div
          className="tile-item-left"
          style={{ transform: `translateY(${translateY}px)` }}
        >
          <div>
            <div className="brandName">{collection.brand}</div>
            <div className="brandInfo">
              {collection.name.split("\n").map((line, i) => (
                <>
                  <span>{line}</span>
                  <br />
                </>
              ))}
            </div>
          </div>
        </div>
        <div className="tile-item-right">
          <div className="tile-item-imgWrapper">
            <img src={publicURL(collection.banners?.img)} alt="" loading="lazy" />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Tile;
