import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import useInput from "../../../hooks/useInput";
import {
  matchPassword,
  clearMatchPassword,
  clearError,
  updateProfile,
} from "../../../slice/authSlice";

import "./profile.scss";

function Profile() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const { matchPwd, error } = useSelector((store) => store.auth);
  const { email, username, mobile } = useSelector((store) => store.user.user);
  const [password, onChangePassword, setPassword] = useInput("");
  const [passwordCheck, setPasswordCheck] = useState("");
console.log({email, mobile});

  const [name, onChangeName, setName] = useInput(username);
  const [phone, onChangePhone, setPhone] = useInput(mobile);

  const checkPassword = (e) => {
    // matchPwd: false
    e.preventDefault();
    const user = {
      email,
      password,
    };
    dispatch(matchPassword(user));
  };

  const onChangePasswordCheck = (e) => {
    setPasswordCheck(e.target.value);
  };

  const onSubmit = (e) => {
    e.preventDefault();

    const regPhone = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/;

    if (!regPhone)
      return alert("  . (ex. 010-0000-0000)");
    if (password.length > 0 && password !== passwordCheck)
      return alert("  .");

    const user = {
      username: name,
      email,
      password,
      mobile: phone,
    };

    dispatch(updateProfile(user));
  };

  useEffect(() => {
    setName(username);
    setPhone(mobile);
  }, [username, mobile]);

  useEffect(() => {
    if (error) {
      alert(error);
      dispatch(clearError());
    }
  }, [error]);

  useEffect(() => {
    dispatch(clearMatchPassword());
  }, [pathname]);

  useEffect(() => {
    setPassword("");
  }, [matchPwd]);

  console.log("hi");
  return (
    <div className="profile">
      {!matchPwd && (
        <div className="profile-item">
          <div className="profile-item-title">
            <h2> </h2>
          </div>

          <div className="profile-item-content">
            <div className="alert">
              <p>
                {username}      <br />
                    .
              </p>
            </div>

            <form onSubmit={checkPassword} className="form">
              <div className="form-input">
                <label htmlFor="user-password"></label>
                <input
                  type="password"
                  id="user-password"
                  placeholder="Password"
                  onChange={onChangePassword}
                />
              </div>
              <button className="form-btn" type="submit">
                
              </button>
            </form>
          </div>
        </div>
      )}

      {matchPwd && (
        <div className="profile-item">
          <div className="profile-item-title">
            <h2> </h2>
          </div>

          <div className="profile-item-content">
            <form onSubmit={onSubmit} className="modifyForm">
              <div className="modifyForm-input">
                <label htmlFor="user-email" className="modifyForm-input-left">
                  
                </label>
                <input
                  className="modifyForm-input-right"
                  id="user-email"
                  type="email"
                  value={email}
                  disabled
                />
              </div>
              <div className="modifyForm-input">
                <label htmlFor="user-name" className="modifyForm-input-left">
                  
                </label>
                <input
                  className="modifyForm-input-right"
                  id="user-name"
                  type="text"
                  value={name}
                  onChange={onChangeName}
                />
              </div>
              <div className="modifyForm-input">
                <label htmlFor="user-phone" className="modifyForm-input-left">
                  
                </label>
                <input
                  className="modifyForm-input-right"
                  id="user-phone"
                  type="text"
                  value={phone}
                  onChange={onChangePhone}
                />
              </div>
              <div className="modifyForm-input">
                <label
                  htmlFor="user-password"
                  className="modifyForm-input-left"
                >
                   
                </label>
                <input
                  className="modifyForm-input-right"
                  id="user-password"
                  type="password"
                  value={password}
                  onChange={onChangePassword}
                  placeholder={"New Password"}
                />
              </div>
              <div className="modifyForm-input">
                <label
                  htmlFor="user-passwordCheck"
                  className="modifyForm-input-left"
                >
                    
                </label>
                <input
                  className="modifyForm-input-right"
                  id="user-passwordCheck"
                  type="password"
                  value={passwordCheck}
                  placeholder="Confirm Password"
                  onChange={onChangePasswordCheck}
                />
              </div>
              <button className="modifyForm-btn" type="submit">
                
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}

export default Profile;
